import React from 'react';
import List from '../components/List';
import Home from '../pages/Home';
function Buttons(){
        const btns = [1,2,3,4,5];
        return <div>
                {
                btns.map(
                    (item)=>(
                        <button
                            key={item}
                            onClick={()=>this.getData(item)}
                            >
                                {item}
                        </button>
                    )
                )
                }
        </div>
}
export default Buttons;
